import React, { useEffect, useState } from 'react';
import {
  Dimensions,
  ScrollView,
  ActivityIndicator,
  View,
  Text,
  TextInput,
  Button,
  Image,
  Alert
} from 'react-native';
import Tweet from './components/Tweet';
import HorizontalDivider from '../../components/HorizontalDivider';
import axios from 'axios';
import * as ImagePicker from 'expo-image-picker';
import { useNavigation } from '@react-navigation/native';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { RootStackParamList } from '../../routes/stack.routes';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { TouchableOpacity } from 'react-native-gesture-handler';
import Ionicons from '@expo/vector-icons/Ionicons';

const dimensions = Dimensions.get('window');

interface Tweet {
  id: number;
  content: string;
  contentImage?: string | null;
  userId: number;
  userName: string;
  profileName: string;
  userAvatar?: string;
  hasImage: boolean;
  dateCreated: string;
}

const Cuitter: React.FC = () => {
  const [tweets, setTweets] = useState<Tweet[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [content, setContent] = useState<string>('');
  const [image, setImage] = useState<any>(null);
  const [imageFile, setImageFile] = useState<File | null>(null);

  const navigation = useNavigation<NativeStackNavigationProp<RootStackParamList>>();

  const getToken = async (): Promise<string | null> => {
    return await AsyncStorage.getItem('userToken');
  };

  const fetchTweets = async () => {
    const token = await getToken();
    try {
      const response = await axios.get<Tweet[]>(`${process.env.REACT_APP_API}/CuitterPost/GetAll`, {
        headers: {
          Authorization: `Bearer ${token}`,
          "ngrok-skip-browser-warning": true,
        },
      });
      setTweets(response.data.reverse());
    } catch (error) {
      console.error('Error fetching tweets:', error);
    } finally {
      setLoading(false);
    }
  };

  const handlePostTweet = async () => {
    if (!content.trim() && !imageFile) {
      Alert.alert('Error', 'Please add content or select an image.');
      return;
    }

    const formData = new FormData();
    formData.append('Content', content);
    formData.append('ProfileId', '1');
    if (imageFile) {
      formData.append('Image', imageFile);
    }

    try {
      const token = await getToken();
      await axios.post(`${process.env.REACT_APP_API}/CuitterPost/PostCuitter`, formData, {
        headers: {
          Authorization: `Bearer ${token}`,
          'Content-Type': 'multipart/form-data',
        },
      });

      setContent('');
      setImage('');
      setImageFile(null);
      Alert.alert('Success', 'Tweet posted successfully!');

      // Fetch tweets again to update the feed
      fetchTweets();
    } catch (error) {
      console.error('Error posting tweet:', error);
      Alert.alert('Error', 'Failed to post tweet.');
    }
  };

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      setImageFile(file);
      const reader = new FileReader();
      reader.onload = (e) => setImage(e.target?.result as string);
      reader.readAsDataURL(file);
    }
  };

  useEffect(() => {
    fetchTweets();
  }, []);

  if (loading) {
    return (
      <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
        <ActivityIndicator size="large" color="#0000ff" />
        <Text>Loading Tweets...</Text>
      </View>
    );
  }

  return (
    <View style={{ height: dimensions.height }}>
      {/* New Tweet Form */}
      <View style={{ flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center', gap: 10, padding: 20, borderBottomWidth: 1, borderColor: '#ccc' }}>
        <TextInput
          style={{
            borderWidth: 1,
            borderColor: '#ccc',
            borderRadius: 8,
            padding: 10,
            width: '100%'
          }}
          placeholder="O que pega?"
          multiline
          value={content}
          onChangeText={setContent}
        />
        {image && (
          <Image source={{ uri: image }} style={{ width: 100, height: 100, marginBottom: 10 }} />
        )}
        <View style={{ flexDirection: 'row', gap: 10 }}>
          <input
            type="file"
            accept="image/*"
            onChange={handleFileChange}
            style={{
              position: 'absolute',
              top: 0,
              left: 0,

              opacity: 0,
              cursor: 'pointer',
            }}
          />
          <TouchableOpacity
            //@ts-ignore
            onPress={() => document.querySelector('input[type="file"]')?.click()}
            style={{
              backgroundColor: '#037ffc',
              height: 50,
              width: 50,
              borderRadius: 100,
              alignItems: 'center',
              justifyContent: 'center'
            }}
          >
            <Ionicons
              name='camera'
              color='white'
              size={20}
            />
          </TouchableOpacity>
          <TouchableOpacity
            onPress={handlePostTweet}
            style={{
              backgroundColor: '#037ffc',
              height: 50,
              width: 50,
              borderRadius: 100,
              alignItems: 'center',
              justifyContent: 'center'
            }}
          >
            <Ionicons
              name='send'
              color='white'
              size={20}
            />
          </TouchableOpacity>
        </View>
      </View>

      {/* Tweet Feed */}
      <ScrollView style={{ padding: 20 }}>
        {tweets.map((tweet) => (
          <View key={tweet.id}>
            <Tweet
              avatarImage={`${process.env.REACT_APP_API}${tweet.userAvatar}`}
              name={tweet.userName}
              profileName={tweet.profileName}
              comments={0}
              likes={0}
              reTweets={0}
              time={new Date(tweet.dateCreated).toLocaleTimeString()}
              hasImage={tweet.hasImage}
              tweetImage={`${process.env.REACT_APP_API}${tweet.contentImage}`}
              remove={() => console.log('Remove tweet clicked')}
              onPressDetail={() => navigation.navigate('DetailCuitte', { postId: tweet.id })}
            >
              {tweet.content}
            </Tweet>
            <HorizontalDivider />
          </View>
        ))}
      </ScrollView>
    </View>
  );
};

export default Cuitter;
