import React, { useEffect, useState, useRef } from 'react';
import {
  View,
  ScrollView,
  Text,
  Alert,
  Modal,
  Animated,
  PanResponder,
  TouchableWithoutFeedback,
} from 'react-native';
import axios from 'axios';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { PicturePost } from './components/PicturePost';
import Comments from '../Profile/components';
import { useIsFocused } from '@react-navigation/native';
import { Buffer } from 'buffer';

interface Post {
  id: number;
  content: string;
  contentImage: string;
  userId: number;
  userName: string;
  profileName: string;
  userAvatar: string;
  hasImage: boolean;
}

const BottomDrawer: React.FC<{
  isVisible: boolean;
  onClose: () => void;
  children: React.ReactNode;
}> = ({ isVisible, onClose, children }) => {
  const animatedValue = useRef(new Animated.Value(0)).current;

  const panResponder = useRef(
    PanResponder.create({
      onMoveShouldSetPanResponder: (_, gestureState) =>
        Math.abs(gestureState.dy) > 5,
      onPanResponderMove: (_, gestureState) => {
        if (gestureState.dy > 0) {
          animatedValue.setValue(gestureState.dy);
        }
      },
      onPanResponderRelease: (_, gestureState) => {
        if (gestureState.dy > 100) {
          onClose();
        } else {
          Animated.spring(animatedValue, {
            toValue: 0,
            useNativeDriver: true,
          }).start();
        }
      },
    })
  ).current;

  useEffect(() => {
    if (isVisible) {
      Animated.timing(animatedValue, {
        toValue: 0,
        duration: 300,
        useNativeDriver: true,
      }).start();
    }
  }, [isVisible]);

  if (!isVisible) return null;

  return (
    <Modal transparent animationType="fade" visible={isVisible}>
      <TouchableWithoutFeedback onPress={onClose}>
        <View style={{ flex: 1, backgroundColor: 'rgba(0,0,0,0.5)' }} />
      </TouchableWithoutFeedback>
      <Animated.View
        {...panResponder.panHandlers}
        style={{
          position: 'absolute',
          bottom: 0,
          left: 0,
          right: 0,
          backgroundColor: '#fff',
          borderTopLeftRadius: 20,
          borderTopRightRadius: 20,
          transform: [{ translateY: animatedValue }],
          padding: 20,
          maxHeight: '70%',
        }}
      >
        {children}
      </Animated.View>
    </Modal>
  );
};

const HomeScreen: React.FC = () => {
  const [posts, setPosts] = useState<Post[]>([]);
  const [isDrawerVisible, setDrawerVisible] = useState(false);
  const [selectedPost, setSelectedPost] = useState<Post | null>(null);
  const [avatar, setAvatar] = useState<string>('');

  const isFocused = useIsFocused();

  useEffect(() => {
    const fetchPosts = async () => {
      try {
        const token = await AsyncStorage.getItem('userToken');

        const response = await axios.get(
          `${process.env.REACT_APP_API}/StrixgamPost/GetAll`,
          {
            headers: {
              Authorization: `Bearer ${token}`,
              "ngrok-skip-browser-warning": true,
            },
          }
        );
        setPosts(response.data);
      } catch (error) {
        console.error('Error fetching posts:', error);
      }
    };

    fetchPosts();
  }, [isFocused]);

  const fetchAvatar = async () => {
    try {
      const token = await AsyncStorage.getItem('userToken');
      const avatarUrl = await AsyncStorage.getItem('avatarUrl');
      if (!token) {
        console.error('No token found');
        return;
      }

      const response = await axios.get(`${process.env.REACT_APP_API}${avatarUrl}`, {
        headers: {
          Authorization: `Bearer ${token}`,
          "ngrok-skip-browser-warning": true,
        },
        responseType: 'arraybuffer',
      });

      const base64Image = `data:image/png;base64,${Buffer.from(response.data).toString('base64')}`;
      setAvatar(base64Image);
    } catch (error) {
      console.error('Error fetching avatar:', error);
    }
  };

  useEffect(() => {
    fetchAvatar();
  }, [])


  const deleteTweet = async (id: number) => {
    try {
      const token = await AsyncStorage.getItem('userToken');
      await axios.delete(`${process.env.REACT_APP_API}/StrixgamPost/Delete/${id}`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      Alert.alert('Success', 'Tweet deleted successfully!');
      setPosts((prevPosts) => prevPosts.filter((post) => post.id !== id));
    } catch (err: any) {
      console.error(err);
      if (err.response?.status === 401) {
        Alert.alert('Error', 'Unauthorized. Please log in.');
      } else {
        Alert.alert('Error', 'Failed to delete post.');
      }
    }
  };

  const openCommentDrawer = (post: Post) => {
    setSelectedPost(post);
    setDrawerVisible(true);
  };

  console.log('PUNK ROCK LOOSER', avatar)

  return (
    <View style={{ flex: 1, padding: 20 }}>
      <ScrollView showsVerticalScrollIndicator={false}>
        {posts.length > 0 ? (
          posts
            .slice()
            .reverse()
            .map((post) => (
              <View key={post.id}>
                <PicturePost.Root>
                  <PicturePost.PostHeader
                    user_thumb={`${process.env.REACT_APP_API}${post.userAvatar}`}
                    user_name={post.userName}
                    location={post.profileName}
                    remove={() => deleteTweet(post.id)}
                  />
                  <PicturePost.PostDescription
                    user_name={post.profileName}
                    description={post.content}
                  />
                  {post.hasImage && (
                    <PicturePost.PostContent
                      post_image={`${process.env.REACT_APP_API}${post.contentImage}`}
                      pressComment={() => openCommentDrawer(post)}
                    />
                  )}
                </PicturePost.Root>


              </View>
            )
            )
        ) : (
          <Text>No posts found.</Text>
        )}
        <BottomDrawer
          isVisible={isDrawerVisible}
          onClose={() => setDrawerVisible(false)}
        >
          {selectedPost ? (
            <View>
              <Text style={{ fontWeight: 'bold', fontSize: 18 }}>
                Comentários na postagem de {selectedPost.userName}
              </Text>
              <Text style={{ marginTop: 10 }}>{selectedPost.content}</Text>
              <Comments avatar={avatar} postId={selectedPost.id} />
            </View>
          ) : (
            <Text>Loading...</Text>
          )}
        </BottomDrawer>
      </ScrollView>
    </View>
  );
};

export default HomeScreen;
