import React, { useEffect, useState } from 'react';
import {
  Dimensions,
  ScrollView,
  View,
  Text,
  StyleSheet,
  ActivityIndicator,
  TouchableOpacity,
  TextInput,
  KeyboardAvoidingView,
  Platform,
  Keyboard,
} from 'react-native';
import { useNavigation, useRoute } from '@react-navigation/native';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { RootStackParamList } from '../../../../routes/stack.routes';
import Tweet from '../Tweet';
import HorizontalDivider from '../../../../components/HorizontalDivider';
import axios from 'axios';
import Ionicons from '@expo/vector-icons/Ionicons';
import AsyncStorage from '@react-native-async-storage/async-storage';

const dimensions = Dimensions.get('window');

const DetailCuitte = () => {
  const navigation = useNavigation<NativeStackNavigationProp<RootStackParamList>>();
  const route = useRoute();
  const { postId } = route.params as { postId: number };

  const [post, setPost] = useState<any>(null);
  const [comments, setComments] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [reply, setReply] = useState('');

  const getToken = async (): Promise<string | null> => {
    const token = await AsyncStorage.getItem('userToken');
    return token;
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        const token = await getToken();
        const response = await axios.get(`${process.env.REACT_APP_API}/CuitterInteraction/GetCommentsForPost/${postId}`, {
          headers: {
            'Content-Type': 'multipart/form-data',
            Authorization: `Bearer ${token}`,
            "ngrok-skip-browser-warning": true,
          },
        });
        setPost(response.data.post);
        setComments(response.data.comments.reverse())
      } catch (error) {
        console.error('Error fetching post and comments:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [postId]);

  const getUsern = async (): Promise<string | null> => {
    return await AsyncStorage.getItem('userId');
  };



  const handleReplySubmit = async () => {
    if (!reply.trim()) return;

    try {
      const user = await getUsern();
      const token = await getToken();
      const response = await axios.post(`${process.env.REACT_APP_API}/CuitterInteraction/CommentOnPost`, {
        PostId: postId,
        UserId: user,
        Content: reply,
      }, {
        headers: {
          // 'Content-Type': 'multipart/form-data',
          Authorization: `Bearer ${token}`,
          "ngrok-skip-browser-warning": true,
        },
      });
      setComments([...comments, response.data]);
      setReply('');
      Keyboard.dismiss();
    } catch (error) {
      console.error('Error submitting reply:', error);
    }
  };

  if (loading) {
    return <ActivityIndicator size="large" color="#0000ff" />;
  }

  if (!post) {
    return (
      <View style={styles.center}>
        <Text style={styles.errorText}>Post not found.</Text>
      </View>
    );
  }

  return (
    <KeyboardAvoidingView
      style={{ flex: 1 }}
      behavior={Platform.OS === 'ios' ? 'padding' : undefined}
    >
      <View style={styles.header}>
        <TouchableOpacity onPress={() => navigation.goBack()}>
          <Ionicons name="arrow-back-outline" size={24} color="black" />
        </TouchableOpacity>
        <Text style={styles.headerTitle}>Postar</Text>
        <View />
      </View>
      <ScrollView style={styles.scrollContainer}>
        <Tweet
          avatarImage={`${process.env.REACT_APP_API}${post.user.avatarUrl}`}
          name={post.user.nome}
          profileName={post.user.username}
          comments={comments.length}
          likes={0}
          reTweets={0}
          time={new Date(post.dateCreated).toLocaleTimeString()}
          hasImage={post.contentImage}
          tweetImage={`${process.env.REACT_APP_API}${post.contentImage}`}
          remove={() => { }}
        >
          {post.content}
        </Tweet>

        {comments.map((comment) => (
          <View style={{ marginLeft: 20 }} key={comment.commentId}>
            <Tweet
              avatarImage={`${process.env.REACT_APP_API}${comment.user.avatarUrl}`}
              name={comment.user.nome}
              profileName={comment.user.username}
              comments={1}
              likes={0}
              reTweets={0}
              time={new Date(comment.dateCreated).toLocaleTimeString()}
              hasImage={post.user.hasImage}
              tweetImage={`${process.env.REACT_APP_API}${comment.contentImage}`}
              remove={() => { }}
            >
              {comment.content}
            </Tweet>
            <HorizontalDivider />
          </View>
        ))}
      </ScrollView>
      <View style={styles.replyContainer}>
        <TextInput
          style={styles.input}
          placeholder="Escreva um comentário..."
          value={reply}
          onChangeText={setReply}
        />
        <TouchableOpacity style={styles.sendButton} onPress={handleReplySubmit}>
          <Ionicons name="send" size={20} color="white" />
        </TouchableOpacity>
      </View>
    </KeyboardAvoidingView>
  );
};

const styles = StyleSheet.create({
  header: {
    flexDirection: 'row',
    alignItems: 'center',
    paddingHorizontal: 20,
    paddingVertical: 10,
    backgroundColor: '#f8f9fa',
    borderBottomWidth: 1,
    borderBottomColor: '#ddd',
    justifyContent: 'space-between',
  },
  headerTitle: {
    fontSize: 16,
    fontWeight: 'bold',
  },
  scrollContainer: {
    flex: 1,
    padding: 20,
  },
  replyContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    padding: 10,
    borderTopWidth: 1,
    borderTopColor: '#ddd',
    backgroundColor: '#fff',
  },
  input: {
    flex: 1,
    height: 40,
    paddingHorizontal: 10,
    borderRadius: 20,
    borderWidth: 1,
    borderColor: '#ccc',
    backgroundColor: '#f8f9fa',
    marginRight: 10,
  },
  sendButton: {
    width: 40,
    height: 40,
    borderRadius: 20,
    backgroundColor: '#007bff',
    justifyContent: 'center',
    alignItems: 'center',
  },
  center: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  errorText: {
    color: 'red',
    fontSize: 18,
  },
});

export default DetailCuitte;
